
export default function createCalendar(year, month, activeDates = []){
  // console.log(year, month);
  var currentDate = new Date(year, month),//,   -.         
      nextDate = new Date(year, month + 1),// ,   
      currentDateDay = new Date(year, month, 1),//   
      periodMs = nextDate - currentDate,//   /      
      days = parseInt(periodMs/(60*60*1000*24)),//    
      daysNames = ['C', '', '', '', '', '', ''],
      n = currentDateDay.getDay() - 1,
      arr = [],
      startDay = currentDateDay.getDay();
  
  for(var i = 0; i < days; i++){
    var activeDate = false;
    activeDates.forEach((day) => {
      if(day.getDate() === i){
        activeDate = true;
      }
    });

    n++;
    if(n > 6 ) n = 0;
    // console.log(" " + (i+1) + "  " + n);
    var cell = {dayOfWeekInd: i + 1, dayName: daysNames[n], dayOfMonth: n, isActiveDay: activeDate};
    arr.push(cell);
  }
  
  return arr;

}
